import React from "react"
import { Container, Typography, List, ListItem, ListItemText, Box, ListItemIcon, IconButton, Divider } from "@mui/material";
import { format, isSameMonth } from "date-fns";
import { fi } from "date-fns/locale";
import { Link } from "react-router-dom";
import DeleteIcon from '@mui/icons-material/Delete';
import { green, pink } from "@mui/material/colors";

interface Props {
    tulot: Tulo[]
    menot: Meno[]
}

const Lista: React.FC<Props> = ({ tulot, menot }): React.ReactElement => {

    const jarjestetytTulot = [...tulot];
    const jarjestetytMenot = [...menot];

    jarjestetytTulot.sort((a, b) => {
        const aAika = new Date(a.pvm);
        const bAika = new Date(b.pvm);
        return bAika.getTime() - aAika.getTime();
    });

    jarjestetytMenot.sort((a, b) => {
        const aAika = new Date(a.pvm);
        const bAika = new Date(b.pvm);
        return bAika.getTime() - aAika.getTime();
    });

    return (
        <Container maxWidth="xs">
            <Typography variant="h6" textAlign="center" sx={{ color: green[500] }}>
                Kaikki tulot (uusin ensin)
            </Typography>

            <List>
                {jarjestetytTulot.map((tulo: Tulo, index: number) => {
                    const edellinenTulo = jarjestetytTulot[index - 1];
                    const kuukaudenVaihtuminen = edellinenTulo
                        ? !isSameMonth(new Date(edellinenTulo.pvm), new Date(tulo.pvm))
                        : true;

                    return (
                        <React.Fragment key={index}>
                            {kuukaudenVaihtuminen && (
                                <>
                                    <Divider />
                                    <ListItem>
                                        <ListItemText
                                            primary={format(new Date(tulo.pvm), "MMMM y", { locale: fi }).replace(/ta/, '')}
                                            sx={{ textAlign: "center" }}
                                        />
                                    </ListItem>
                                    <Divider />
                                </>
                            )}

                            <ListItem sx={{ backgroundColor: index % 2 === 0 ? green[50] : '#ffffff' }}>
                                <ListItemText
                                    primary={format(new Date(tulo.pvm), "d.M.yyyy", { locale: fi })}
                                    secondary={
                                        <>
                                            <Typography variant="body2" color="textSecondary">
                                                Mr: {tulo.summa} 
                                            </Typography>
                                            <Typography variant="body2" color="textSecondary">
                                                Laji: {tulo.laji}
                                            </Typography>
                                            <Typography variant="body2" color="textSecondary">
                                                Nimi: {tulo.nimi}
                                            </Typography>
                                        </>
                                    }
                                />
                                <ListItemIcon>
                                    <IconButton component={Link} to={`/poistatulo/${tulo.id}`} edge="end">
                                        <DeleteIcon />
                                    </IconButton>
                                </ListItemIcon>
                            </ListItem>
                        </React.Fragment>
                    );
                })}
            </List>

            <Typography variant="h6" textAlign="center" sx={{ marginTop: "25px", color: pink[500] }}>
                Kaikki menot (uusin ensin)
            </Typography>

            <List>
                {jarjestetytMenot.map((meno: Meno, index: number) => {
                    const edellinenMeno = jarjestetytMenot[index - 1];
                    const kuukaudenVaihtuminen = edellinenMeno
                        ? !isSameMonth(new Date(edellinenMeno.pvm), new Date(meno.pvm))
                        : true;

                    return (
                        <React.Fragment key={index}>
                            {kuukaudenVaihtuminen && (
                                <>
                                    <Divider />
                                    <ListItem>
                                        <ListItemText
                                            primary={format(new Date(meno.pvm), "MMMM y", { locale: fi }).replace(/ta/, '')}
                                            sx={{ textAlign: "center" }}
                                        />
                                    </ListItem>
                                    <Divider />
                                </>
                            )}

                            <ListItem sx={{ backgroundColor: index % 2 === 0 ? pink[50] : '#ffffff' }}>
                                <ListItemText
                                    primary={format(new Date(meno.pvm), "d.M.yyyy", { locale: fi })}
                                    secondary={
                                        <>
                                            <Typography variant="body2" color="textSecondary">
                                                Mr: {meno.summa} 
                                            </Typography>
                                            <Typography variant="body2" color="textSecondary">
                                                Laji: {meno.laji}
                                            </Typography>
                                            <Typography variant="body2" color="textSecondary">
                                                Nimi: {meno.nimi}
                                            </Typography>
                                        </>
                                    }
                                />
                                <ListItemIcon>
                                    <IconButton component={Link} to={`/poistameno/${meno.id}`} edge="end">
                                        <DeleteIcon />
                                    </IconButton>
                                </ListItemIcon>
                            </ListItem>
                        </React.Fragment>
                    );
                })}
            </List>

            <Box height={60} />
        </Container>
    );
};

export default Lista;