import React, { useState } from 'react';
import { FormControl, Container, Typography, TextField, Button, Radio, RadioGroup, FormControlLabel, FormLabel } from '@mui/material';
import { SelectChangeEvent } from '@mui/material/Select';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { fi } from 'date-fns/locale';
import { v4 as uuid } from 'uuid';
import { Link, NavigateFunction, useNavigate } from 'react-router-dom';
import { green } from '@mui/material/colors';
import AddCircleIcon from '@mui/icons-material/AddCircle';

interface Props {
    tulot: Tulo[];
    setTulot: (arg0: Tulo[]) => void;
}

const Tulo: React.FC<Props> = ({ tulot, setTulot }): React.ReactElement => {

    const [pvm, setPvm] = useState<Date>(new Date());
    const [laji, setLaji] = useState<string>("");
    const [nimi, setNimi] = useState<string>("");
    const [summa, setSumma] = useState<number | undefined>();

    const navigate: NavigateFunction = useNavigate();

    const pvmKasittelija = (valittuPvm: Date | null) => {
        setPvm(valittuPvm || new Date());
    };

    const lajiKasittelija = (e: SelectChangeEvent) => {
        setLaji(e.target.value as string);
    };

    const lisaaTulo = (e: React.FormEvent<HTMLFormElement>): void => {
        e.preventDefault();

        if (pvm && laji && summa) {
            let apuTulo: Tulo = {
                id: uuid(),
                pvm: pvm,
                laji: laji,
                nimi: nimi,
                summa: Number(summa),
            };

            setTulot([...tulot, apuTulo]);
            navigate("/yhteenveto");
        }
    };

    return (
        <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
            <Container maxWidth="xs">
                <Typography variant="h4" sx={{ marginBottom: "20px", color: "#2e74b7" }}>Lis uusi tulo</Typography>

                <form onSubmit={lisaaTulo}>

                    <DatePicker
                        label="Pivmr"
                        disableFuture
                        value={pvm}
                        onChange={pvmKasittelija}
                        slotProps={{
                            textField: {
                                required: true,
                                fullWidth: true,
                            },
                        }}
                    />

                    <FormControl fullWidth sx={{ marginTop: "20px" }}>
                        <FormLabel required id="tulolaji">Valitse tulolaji</FormLabel>
                        <RadioGroup
                            aria-labelledby="tulolaji"
                            name="radio-buttons-group"
                            onChange={lajiKasittelija}
                        >
                            <FormControlLabel value="palkka- ja yrittjtulot" control={<Radio />} label="palkka- ja yrittjtulot" />
                            <FormControlLabel value="elkkeet ja tuet" control={<Radio />} label="elkkeet ja tuet" />
                            <FormControlLabel value="muut tulot" control={<Radio />} label="muut tulot" />
                        </RadioGroup>
                    </FormControl>

                    <TextField
                        label="Tarkennettu tulonimi"
                        placeholder='Tarkennettu tulonimi'
                        variant="outlined"
                        required
                        fullWidth={true}
                        sx={{ marginTop: "10px", marginBottom: "10px" }}
                        onChange={(e) => setNimi(e.target.value)}
                    />

                    <TextField
                        label="Summa"
                        placeholder='Summa'
                        variant="outlined"
                        fullWidth={true}
                        required
                        type="number"
                        sx={{ marginBottom: "25px" }}
                        onChange={(e) => setSumma(Number(e.target.value))}
                    />

                    <Button
                        type="submit"
                        size='large'
                        fullWidth
                        variant="outlined"
                        startIcon={<AddCircleIcon sx={{ color: green[500] }} />}
                        sx={{ marginBottom: '5px' }}
                    >Lis tulo</Button>

                    <Button
                        component={Link}
                        to="/"
                        fullWidth
                    >Peruuta</Button>
                </form>
            </Container>
        </LocalizationProvider>
    );
};

export default Tulo;
