import React from 'react'
import { Button, Container, Typography } from '@mui/material';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { fi } from 'date-fns/locale';
import { format } from 'date-fns';
import DeleteIcon from '@mui/icons-material/Delete';
import { pink } from '@mui/material/colors';

interface Props {
    menot: Meno[];
    setMenot: (arg0: Meno[]) => void;
}

const PoistaMeno: React.FC<Props> = ({ menot, setMenot }): React.ReactElement => {

    const navigate: NavigateFunction = useNavigate();
    const { id } = useParams<any>();
    // indeksin poimimista varten useParams, vasemmalle puolelle objekti, joka halutaan ottaa parametrina

    const poistettavaMeno: Meno | undefined = menot.find((meno: Meno) => {
        return meno.id === id;
    });

    const vahvistaPoisto = (): void => {

        setMenot([...menot.filter((meno: Meno) => meno.id !== id)]); //filtteridn menot

        navigate("/lista");
    }

    return (
        <>
            <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>

                <Container maxWidth="xs">
                    <Typography variant='h4' sx={{ color: pink[500], marginTop: "10px", marginBottom: "10px" }}>
                        Poista meno</Typography>

                    {/* huutomerkill voidaan ns. pakottaa tai ohittaa suorittamaan eli kun tm luulee, ett on mahdollista olla undefined niin nyt ! pakottaa jatkamaan siit huolimatta */}

                    <Typography
                    >Haluatko varmasti poistaa:
                    </Typography>
                    <Typography
                        sx={{ marginBottom: "20px;" }}>
                        {poistettavaMeno!.nimi} {poistettavaMeno!.pvm
                            ? format(new Date(poistettavaMeno!.pvm),
                                "d.M.yyyy", { locale: fi })
                            : "Ei saatavilla"}, {poistettavaMeno!.laji}, {poistettavaMeno!.summa} ?
                    </Typography>

                    <Button
                        size='large'
                        fullWidth
                        variant="outlined"
                        onClick={vahvistaPoisto}
                        startIcon={<DeleteIcon />}
                        sx={{ marginTop: "5px", marginBottom: "5px" }}
                    >Poista meno</Button>


                    <Button
                        fullWidth={true}
                        component={Link}
                        to="/lista"
                        size="large"
                    >Peruuta</Button>
                </Container>
            </LocalizationProvider>
        </>
    )
}

export default PoistaMeno;