import { Container, Typography, Button, Paper } from "@mui/material";
import { green, pink } from "@mui/material/colors";
import { Link } from "react-router-dom";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import { LocalizationProvider } from "@mui/x-date-pickers";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { format } from "date-fns";
import { fi } from "date-fns/locale";

interface Props {
    tulot: Tulo[];
    menot: Meno[];
}

const Aloitus: React.FC<Props> = ({ tulot, menot }) => {

    const pvmNyt = new Date();
    const laskeSumma = (laji: string, tapahtumat: (Tulo | Meno)[]) => {
        return tapahtumat
            .filter((tapahtuma) => tapahtuma.laji === laji)
            .reduce((summa, tapahtuma) => summa + tapahtuma.summa, 0);
    };

    const palkkaTulot = laskeSumma('palkka- ja yrittjtulot', tulot);
    const tukiTulot = laskeSumma('elkkeet ja tuet', tulot);
    const muutTulot = laskeSumma('muut tulot', tulot);

    const arkiMenot = laskeSumma('arkimenot', menot);
    const asuminen = laskeSumma('asuminen', menot);
    const liikkuminen = laskeSumma('liikkuminen', menot);

    const tulotYhteensa = palkkaTulot + tukiTulot + muutTulot;
    const menotYhteensa = arkiMenot + asuminen + liikkuminen;
    const tilanne = tulotYhteensa - menotYhteensa;

    return (
        <>
            <Container maxWidth="xs" sx={{ marginTop: '50px' }}>
                <Typography variant="h4" sx={{ color: "#2e74b7" }} gutterBottom>
                    Suunnittele ja ennakoi menosi budjettilaskurilla!
                </Typography>
                <Typography variant="body1" paragraph>
                    Budjettilaskurilla voit suunnitella kuukausittaiset menosi. Menojen budjetoiminen etukteen auttaa hahmottamaan, mihin kaikkeen rahaa on varattava kuukauden aikana. Aloita lismll uusi tulo tai uusi meno ja ne, miten budjettisi kehittyy ajan myt.
                </Typography>

                <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
                    <Paper
                        elevation={0}
                        sx={{
                            marginBottom: "25px",
                            padding: "20px",
                            backgroundColor: tilanne > 0
                                ? green[500]
                                : pink[500],
                        }}
                    >
                        <Typography variant="h4" textAlign="center" color="white">
                            Tilanne {format(pvmNyt, "d.M.yyyy", { locale: fi })} <br />{tilanne > 0
                                ? '+ '
                                : ' '}{tilanne} 
                        </Typography>
                    </Paper>
                </LocalizationProvider>
                <Button
                    size='large'
                    fullWidth
                    variant="outlined"
                    startIcon={<AddCircleIcon sx={{ color: green[500] }} />}
                    component={Link}
                    to={'/tulo'}
                    sx={{ marginBottom: '10px'}}
                >Lis tulo</Button>

                <Button
                    size='large'
                    fullWidth
                    variant="outlined"
                    startIcon={<AddCircleIcon sx={{ color: pink[500] }} />}
                    component={Link}
                    to={'/meno'}
                    sx={{ marginBottom: '25px'}}
                >Lis meno</Button>
            </Container>
        </>
    );
}
export default Aloitus;