import { Button, Container, Paper, Typography } from '@mui/material';
import { green, pink } from '@mui/material/colors';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { format } from 'date-fns';
import { fi } from 'date-fns/locale';
import React, { useState } from 'react';

interface Props {
    tulot: Tulo[];
    menot: Meno[];
}

const Yhteenveto: React.FC<Props> = ({ tulot, menot }) => {

    const pvmNyt = new Date();

    // laske summa tuloista tai menoista kullekin lajille
    const laskeSumma = (laji: string, tapahtumat: (Tulo | Meno)[]) => {
        return tapahtumat
            .filter((tapahtuma) => tapahtuma.laji === laji)
            .reduce((summa, tapahtuma) => summa + tapahtuma.summa, 0);
    };

    // lajittele tulot ja menot
    const palkkaTulot = laskeSumma('palkka- ja yrittjtulot', tulot);
    const tukiTulot = laskeSumma('elkkeet ja tuet', tulot);
    const muutTulot = laskeSumma('muut tulot', tulot);

    const arkiMenot = laskeSumma('arkimenot', menot);
    const asuminen = laskeSumma('asuminen', menot);
    const liikkuminen = laskeSumma('liikkuminen', menot);

    const tulotYhteensa = palkkaTulot + tukiTulot + muutTulot;
    const menotYhteensa = arkiMenot + asuminen + liikkuminen;
    const tilanne = tulotYhteensa - menotYhteensa;

    return (
        <>
            <Container maxWidth="xs">
                <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>

                    <Paper
                        elevation={0}
                        sx={{
                            marginBottom: "25px",
                            padding: "20px",
                            backgroundColor: tilanne > 0 
                            ? green[500] 
                            : pink[500],
                        }}
                    >
                        <Typography variant="h4" textAlign="center" color="white">
                            Tilanne {format(pvmNyt, "d.M.yyyy", { locale: fi })} <br />{tilanne > 0 
                            ? '+ ' 
                            : ' '}{tilanne} 
                        </Typography>
                    </Paper>

                    <Paper
                        elevation={0}
                        sx={{
                            padding: "10px",
                            borderColor: green[500],
                            borderStyle: "solid",
                            borderWidth: "2px",
                            marginBottom: "10px",
                        }}
                    >
                    <Typography variant="h6" textAlign="center" sx={{ marginBottom: "5px", padding: "10px", color: green[500] }}>Tulot yhteens {tulotYhteensa} 
                    </Typography>
                    <Typography variant="body1" textAlign="center">
                        Palkka ja yrittjtulot: {palkkaTulot} 
                    </Typography>
                    <Typography variant="body1" textAlign="center">
                        Elkkeet ja tuet: {tukiTulot} 
                    </Typography>
                    <Typography variant="body1" textAlign="center" paragraph>
                        Muut tulot: {muutTulot} 
                    </Typography>
                    </Paper>

                    <Paper
                        elevation={0}
                        sx={{
                            padding: "10px",
                            borderColor: pink[500],
                            borderStyle: "solid",
                            borderWidth: "2px",
                            marginBottom: "10px",
                        }}
                    >
                    <Typography variant="h6" textAlign="center" sx={{ marginBottom: "5px", padding: "10px", color: pink[500] }}>Menot yhteens {menotYhteensa} 
                    </Typography>
                    <Typography variant="body1" textAlign="center">
                        Arkimenot: {arkiMenot} 
                    </Typography>

                    <Typography variant="body1" textAlign="center">
                        Asuminen: {asuminen} 
                    </Typography>

                    <Typography variant="body1" textAlign="center" paragraph>
                        Liikkuminen: {liikkuminen} 
                    </Typography>
                    </Paper>
                </LocalizationProvider>
            </Container >
        </>
    );
};

export default Yhteenveto;
