import React from 'react'
import { Button, Container, Typography } from '@mui/material';
import { Link, NavigateFunction, useNavigate, useParams } from 'react-router-dom';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { fi } from 'date-fns/locale';
import { format } from 'date-fns';
import DeleteIcon from '@mui/icons-material/Delete';
import { green } from '@mui/material/colors';

interface Props {
    tulot: Tulo[];
    setTulot: (arg0: Tulo[]) => void;
}

const PoistaTulo: React.FC<Props> = ({ tulot, setTulot }): React.ReactElement => {

    const navigate: NavigateFunction = useNavigate();
    const { id } = useParams<any>();

    // tss tuodaan indeksin poimimista varten useParams ja vasemmalle puolelle puretaan objekti, joka halutaan ottaa parametrina, parametrit nyt any-tyyppi

    const poistettavaTulo: Tulo | undefined = tulot.find((tulo: Tulo) => {
        return tulo.id === id;
    });

    const vahvistaPoisto = (): void => {

        setTulot([...tulot.filter((tulo: Tulo) => tulo.id !== id)]); //filtteridn havainnot

        navigate("/lista");
    }

    return (
        <>
            <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>

                <Container maxWidth="xs">
                    <Typography variant='h4' sx={{ color: green[500], marginTop: "10px", marginBottom: "10px" }}>
                        Poista tulo</Typography>

                    {/* huutomerkill voidaan ns. pakottaa tai ohittaa suorittamaan eli kun tm luulee, ett on mahdollista olla undefined niin nyt ! pakottaa jatkamaan siit huolimatta */}

                    <Typography
                    >Haluatko varmasti poistaa:
                    </Typography>
                    <Typography
                        sx={{ marginBottom: "20px;" }}>
                        {poistettavaTulo!.nimi} {poistettavaTulo!.pvm
                            ? format(new Date(poistettavaTulo!.pvm),
                                "d.M.yyyy", { locale: fi })
                            : "Ei saatavilla"}, {poistettavaTulo!.laji}, {poistettavaTulo!.summa} ?
                    </Typography>

                    <Button
                        size='large'
                        fullWidth
                        variant="outlined"
                        onClick={vahvistaPoisto}
                        startIcon={<DeleteIcon />}
                        sx={{ marginTop: "5px", marginBottom: "5px" }}
                    >Poista tulo</Button>

                    <Button
                        fullWidth={true}
                        component={Link}
                        to="/lista"
                        size="large"
                    >Peruuta</Button>
                </Container>
            </LocalizationProvider>
        </>
    )
}

export default PoistaTulo;