import React, { useState } from 'react';
import { FormControl, Container, Typography, TextField, Button, FormControlLabel, FormLabel, Radio, RadioGroup } from '@mui/material';
import { SelectChangeEvent } from '@mui/material/Select';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { fi } from 'date-fns/locale';
import { v4 as uuid } from 'uuid';
import { Link, NavigateFunction, useNavigate } from 'react-router-dom';
import { pink } from '@mui/material/colors';
import AddCircleIcon from '@mui/icons-material/AddCircle';

interface Props {
    menot: Meno[];
    setMenot: (arg0: Meno[]) => void;
}

const Meno: React.FC<Props> = ({ menot, setMenot }): React.ReactElement => {

    const [pvm, setPvm] = useState<Date>(new Date());
    const [laji, setLaji] = useState<string>("");
    const [nimi, setNimi] = useState<string>("");
    const [summa, setSumma] = useState<number | undefined>();

    const navigate: NavigateFunction = useNavigate();

    const pvmKasittelija = (valittuPvm: Date | null) => {
        setPvm(valittuPvm || new Date());
    };

    const lajiKasittelija = (e: SelectChangeEvent) => {
        setLaji(e.target.value as string);
    };

    const lisaaMeno = (e: React.FormEvent<HTMLFormElement>): void => {
        e.preventDefault();

        if (pvm && laji && nimi && summa) {
            let apuMeno: Meno = {
                id: uuid(),
                pvm: pvm,
                laji: laji,
                nimi: nimi,
                summa: Number(summa),
            };

            setMenot([...menot, apuMeno]);
            navigate("/yhteenveto");
        }
    };

    return (
        <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>
            <Container maxWidth="xs">
                <Typography variant="h4" sx={{ marginBottom: "20px", color: "#2e74b7" }}>Lis uusi meno</Typography>

                <form onSubmit={lisaaMeno}>

                    <DatePicker
                        label="Pivmr"
                        disableFuture
                        value={pvm}
                        onChange={pvmKasittelija}
                        slotProps={{
                            textField: {
                                required: true,
                                fullWidth: true,
                            },
                        }}
                    />

                    <FormControl fullWidth sx={{ marginTop: "20px" }}>
                        <FormLabel required id="menolaji">Valitse menolaji</FormLabel>
                        <RadioGroup
                            aria-labelledby="menolaji"
                            name="radio-buttons-group"
                            onChange={lajiKasittelija}
                        >
                            <FormControlLabel value="arkimenot" control={<Radio />} label="arkimenot" />
                            <FormControlLabel value="asuminen" control={<Radio />} label="asuminen" />
                            <FormControlLabel value="liikkuminen" control={<Radio />} label="liikkuminen" />
                        </RadioGroup>
                    </FormControl>

                    <TextField
                        label="Tarkennettu menoer"
                        placeholder='Tarkennettu menoer'
                        variant="outlined"
                        required
                        fullWidth={true}
                        sx={{ marginTop: "10px", marginBottom: "10px" }}
                        onChange={(e) => setNimi(e.target.value)}
                    />

                    <TextField
                        label="Summa"
                        placeholder='Summa'
                        variant="outlined"
                        fullWidth={true}
                        required
                        type="number"
                        sx={{ marginBottom: "25px" }}
                        onChange={(e) => setSumma(Number(e.target.value))}
                    />

                    <Button
                        type="submit"
                        size='large'
                        fullWidth
                        variant="outlined"
                        startIcon={<AddCircleIcon sx={{ color: pink[500] }} />}
                        sx={{ marginBottom: '5px' }}
                    >Lis meno</Button>

                    <Button
                        component={Link}
                        to="/"
                        fullWidth
                    >Peruuta</Button>
                </form>
            </Container>
        </LocalizationProvider>
    );
};

export default Meno;
