import React, { useEffect, useRef, useState } from 'react';
import { Route, Routes } from 'react-router-dom';
import Valikko from './components/Valikko';
import Tulo from './components/Tulo';
import Header from './components/Header';
import Meno from './components/Meno';
import Aloitus from './components/Aloitus';
import Yhteenveto from './components/Yhteenveto';
import Lista from './components/Lista';
import PoistaTulo from './components/PoistaTulo';
import PoistaMeno from './components/PoistaMeno';

const App: React.FC = (): React.ReactElement => {

  const kaynnistetty: React.MutableRefObject<boolean> = useRef(false);
  const [menot, setMenot] = useState<Meno[]>([]);
  const [tulot, setTulot] = useState<Tulo[]>([]);

  useEffect(() => {
    if (!kaynnistetty.current) {
      if (localStorage.getItem("tulot")) {
        setTulot(JSON.parse(String(localStorage.getItem("tulot"))).map((tulo: Tulo) => {
          return {
            ...tulo,
            tuloPvm: new Date(tulo.pvm)
          };
        }))
      }

      if (localStorage.getItem("menot")) {
        setMenot(JSON.parse(String(localStorage.getItem("menot"))).map((meno: Meno) => {
          return {
            ...meno,
            menoPvm: new Date(meno.pvm)
          }
        }))
      }
    }

    return () => {
      kaynnistetty.current = true;
    }

  }, []);


  useEffect(() => {
    localStorage.setItem("tulot", JSON.stringify(tulot));
    localStorage.setItem("menot", JSON.stringify(menot));
  }, [tulot, menot]);

  return (
    <>
      <Header />
      <Routes>
        <Route path="/" element={<Aloitus tulot={tulot} menot={menot} />} />
        <Route path="/yhteenveto" element={<Yhteenveto tulot={tulot} menot={menot} />} />
        <Route path="/lista" element={<Lista tulot={tulot} menot={menot} />} />
        <Route path="/poistatulo/:id" element={<PoistaTulo tulot={tulot} setTulot={setTulot} />} />
        <Route path="/poistameno/:id" element={<PoistaMeno menot={menot} setMenot={setMenot} />} />
        <Route path="/tulo" element={<Tulo tulot={tulot} setTulot={setTulot} />} />
        <Route path="/meno" element={<Meno menot={menot} setMenot={setMenot} />} />
      </Routes>
      <Valikko />
    </>
  );
}

export default App;